import React from "react";

import "./Label.scss";

const Label = props => {
  const style = {
    width: `${props.width}%`,
    marginTop: `${props.marginTop}px`,
    cursor: props.button && "pointer"
  };
  const classStyle = props.light ? "label label__light" : "label";
  return (
    <p style={style} className={classStyle}>
      {props.text}
    </p>
  );
};

export { Label };
