import React from "react";
import "./UserMenu.scss";

const UserMenu = props => {
  const avatar = require("../img/me.png");
  const actions = ["Action 1", "Action 2", "Action 3"];
  return (
    <div className="usermenu">
      <div className="usermenu__button">
        <img alt="avatar" src={avatar} className="usermenu__avatar" />
        <p className="usermenu__username">{props.username}</p>
        <i className="fa fa-angle-down" />
      </div>
      <div className="usermenu__dropdown">
        {actions.map((action, key) => (
          <div className="usermenu__link" key={key}>
            <p>{action}</p>
          </div>
        ))}
        <hr />
        <div className="usermenu__link" onClick={() => props.logout()}>
          <p>Logout</p>
        </div>
      </div>
    </div>
  );
};

export default UserMenu;
