import React from "react";
import { Card, FormRow } from "components/Common";
import "./CompletedDocuments.scss";

const CompletedDocuments = () => {
  return (
    <Card title="Completed Documents" collapse={false} openByDefault={false}>
      <FormRow>
        <p className="light">No documents have been completed..</p>
      </FormRow>
    </Card>
  );
};

export default CompletedDocuments;
