import React, { Component } from "react";
import moment from "moment";
import {
  FormGroup,
  FormRow,
  Button,
  ButtonContainer,
  Input
} from "components/Common";

export default class UpdateDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      updating: false,
      bluedogCaseRef: props.case.bluedogCaseRef,
      firstName: props.case.firstName,
      lastName: props.case.lastName,
      email: props.case.email,
      dateOfBirth: props.case.dateOfBirth,
      address1: props.case.address1,
      address2: props.case.address2,
      address3: props.case.address3,
      address4: props.case.address4,
      houseNo: props.case.houseNo,
      postCode: props.case.postCode
    };
  }
  handleChange = (e, { name, value }) => {
    this.setState({ [name]: value });
  };
  updateDetails = () => {
    const updatedDetails = {
      bluedogCaseRef: this.state.bluedogCaseRef,
      firstName: this.state.firstName,
      lastName: this.state.lastName,
      email: this.state.email,
      dateOfBirth: this.state.dateOfBirth,
      address1: this.state.address1,
      address2: this.state.address2,
      address3: this.state.address3,
      address4: this.state.address4,
      houseNo: this.state.houseNo,
      postCode: this.state.postCode
    };

    this.props.updateDetails(updatedDetails);
  };
  render() {
    const {
      firstName,
      lastName,
      email,
      dateOfBirth,
      address1,
      address2,
      address3,
      address4,
      houseNo,
      postCode
    } = this.state;
    return (
      <div>
        <FormRow marginBottom="10">
          <FormGroup inline={true}>
            <p>First Name: </p>
            <Input
              value={firstName}
              name="firstName"
              onChange={this.handleChange}
            />
          </FormGroup>
          <FormGroup inline={true}>
            <p>Last Name: </p>
            <Input
              value={lastName}
              name="lastName"
              onChange={this.handleChange}
            />
          </FormGroup>
        </FormRow>
        <FormRow marginBottom="10">
          <FormGroup inline={true}>
            <p>House No: </p>
            <Input
              value={houseNo}
              name="houseNo"
              onChange={this.handleChange}
            />
          </FormGroup>
          <FormGroup inline={true}>
            <p>Address 1: </p>
            <Input
              value={address1}
              name="address1"
              onChange={this.handleChange}
            />
          </FormGroup>
        </FormRow>
        <FormRow marginBottom="10">
          <FormGroup inline={true}>
            <p>Address 2: </p>
            <Input
              value={address2}
              name="address2"
              onChange={this.handleChange}
            />
          </FormGroup>
          <FormGroup inline={true}>
            <p>Address 3: </p>
            <Input
              value={address3}
              name="address3"
              onChange={this.handleChange}
            />
          </FormGroup>
        </FormRow>
        <FormRow marginBottom="10">
          <FormGroup inline={true}>
            <p>Address 4: </p>
            <Input
              value={address4}
              name="address4"
              onChange={this.handleChange}
            />
          </FormGroup>
          <FormGroup inline={true}>
            <p>Postcode: </p>
            <Input
              value={postCode}
              name="postCode"
              onChange={this.handleChange}
            />
          </FormGroup>
        </FormRow>
        <FormRow marginBottom="10">
          <FormGroup inline={true}>
            <p>Email: </p>
            <Input value={email} name="email" onChange={this.handleChange} />
          </FormGroup>
          <FormGroup inline={true}>
            <p>Date of Birth: </p>
            <Input
              value={moment(dateOfBirth).format("DD/MM/YYYY")}
              name="dateOfBirth"
              onChange={this.handleChange}
            />
          </FormGroup>
        </FormRow>
        <ButtonContainer marginTop={40} justifyContent="flex-end">
          <Button content="Cancel" secondary onClick={this.props.switchForms} />
          <Button content="Save" primary onClick={this.updateDetails} />
        </ButtonContainer>
      </div>
    );
  }
}
