import React from "react";

const Survey = props => {
  const { name, completed, path, completedBy } = props.survey;
  const style = {
    backgroundColor: completed ? "rgba(27, 170, 87, .2)" : null
  };
  return (
    <div
      style={style}
      onClick={() => props.selectSurvey(path)}
      className="survey"
    >
      <p>{name}</p>
      {completed && <p>Completed by {completedBy}</p>}
    </div>
  );
};

export default Survey;
