import React, { Component } from "react";
import Modal from "react-modal";
import {
  ButtonContainer,
  Button,
  Label,
  Dropdown,
  Form,
  FormRow,
  FormGroup,
  TextArea
} from "components/Common";

import "./CaseModal.scss";

export default class CaseModal extends Component {
  render() {
    const reasons = [
      {
        text: "The client feels physiotherapy will not benefit them",
        value: "1"
      },
      {
        text: "The client is concerned about being liable for payment",
        value: "2"
      }
    ];
    const customStyles = {
      content: {
        height: `${this.props.modalHeight}px`
      }
    };
    const bodyStyles = {
      height: `${this.props.bodyHeight}px`
    };
    return (
      <Modal
        ariaHideApp={false}
        isOpen={this.props.isModalOpen}
        contentLabel="Case Modal"
        className="case-modal"
        style={customStyles}
      >
        <div className="case-modal__title">
          <h3>{this.props.title}</h3>
        </div>
        <hr />
        <div style={bodyStyles} className="case-modal__body">
          {this.props.text !== undefined && (
            <FormRow marginBottom="25">
              <p className="case-modal__text">{this.props.text}</p>
            </FormRow>
          )}
          <FormRow marginBottom="25">
            <FormGroup flexBasis="100">
              <Label width="100" text={this.props.reasonText} />
              <Dropdown
                selection
                options={reasons}
                placeholder="Select Reason.."
              />
            </FormGroup>
          </FormRow>
          <FormRow>
            <FormGroup flexBasis="100">
              <Label width="100" text="Additional Information" />
              <Form>
                <TextArea />
              </Form>
            </FormGroup>
          </FormRow>
        </div>
        <hr />
        <div className="case-modal__footer">
          <ButtonContainer justifyContent="flex-end" marginTop="25">
            <Button
              content="Cancel"
              secondary
              onClick={this.props.closeModal}
            />
            <Button content={this.props.buttonContent} primary />
          </ButtonContainer>
        </div>
      </Modal>
    );
  }
}
